

import Checklist from 'vux/src/components/checklist/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
components: {
XHeader,
Checklist,
Confirm,
},
mounted() {
this.pageName = this.$route.query.name||'选择';
this.typeId = this.$route.query.typeId||'4';
this.dictId = this.$route.query.dictId||0;
this.$store.dispatch("initDictPromise", this.typeId).then(() => {
this.initData();
});
},
data() {
return {
typeId:"",
dictId:0,
value:[],
pageName:"",
list: [],
showAdd:false,
};
},
methods: {
initData() {
this.value.push(this.dictId);
let _list = this.$store.state.dict[this.typeId]?this.$store.state.dict[this.typeId]:[];
_list.map(element =>{
this.list.push({
value: element.DictName,
key: element.ID
})
})
},
addDictItem(){
this.showAdd =true
},
closeConfirm(value){
console.log(value)
},
changeValue(value, label) {

if (value[0] !== undefined) {
if(value[0] == this.dictId){
return false
}
console.log("emit", "dictSelected" + this.typeId)
this.$bus.emit("dictSelected"+this.typeId, {
value: value[0],
label: label[0]
});
this.$router.back();
}
}
},

};



